.header {
  width: 100vw;
  height: 80px;
  background: #61bc84;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  float: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  .logo {
    height: 80%;
    position: absolute;
    left: 10%;
    background: transparent;
    transition: .7s;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;

    >img {
      width: 100%;
      height: 90%;
      margin: 1vh 1vw 1vh 1vw;
    }
  }

  .nav {
    height: 60%;
    position: absolute;
    right: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    .list {
      height: 100%;
      background: #8FBC8F;
      border-radius: 15px;
      position: relative;
      right: 20px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;

      >li {
        height: 80%;
        position: relative;

        &.active {
          >a {

            background: #61bc84;

            >p {
              color: #e0e0e0;
            }
          }

        }

        &:hover {
          >a {
            >p {
              .arrow {
                transform: scale(1.1) translateY(2px);
              }
            }
          }

          >div {
            height: auto;
          }
        }

        >a {
          height: 100%;
          border-radius: 15px;
          padding: 0 1vw 0 1vw;
          margin: 0 .5vw 0 .5vw;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          background: transparent;
          position: relative;

          >p {
            color: #1d1e22;
            font-size: 14px;
            font-weight: bold;
            letter-spacing: 1px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            .arrow {
              transition: .7s;
            }
          }

        }

        >div {
          transition: .7s;
          width: 100%;
          height: 0;
          overflow: hidden;
          position: absolute;
          background: #8FBC8F;

          >ul {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: 10px;
            margin-bottom: 10px;

            >li {
              margin: 1vh 0 1vh 0;
              width: 80%;
              position: relative;

              &.active {
                &:before {
                  content: '';
                  width: 100%;
                  height: 1px;
                  background: #1d1e22;
                  position: absolute;
                  bottom: 0;
                }
              }

              >a {
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: center;
                color: #1d1e22;
                font-size: 14px;
                font-weight: bold;
              }
            }
          }
        }
      }
    }

    .user {
      height: 100%;
      background: #8FBC8F;
      border-radius: 15px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      transition: .5s;

      >p {
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 5px;
        padding: 0 1vw 0 1vw;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
      }
    }
  }

  &::before {
    width: 100%;
    height: 10px;
    position: absolute;
    bottom: -5px;
    content: "";
    background: linear-gradient(to bottom, #61bc84 10%, transparent 50%);
  }
}