.home {
  width: 100vw;

  .page {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;

    .bg {
      width: 100%;
      height: 100%;
      position: absolute;
    }

    .mo {
      width: 100%;
      height: 100%;
      background: #ffffff50;
      position: absolute;
      z-index: 4;
    }

    .title {
      z-index: 5;

      >p {
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 2px;
      }
    }
  }
}