.footer {
  width: 100vw;
  height: 50vh;
  background: #61bc84;
  display: flex;
  align-items: center;
  justify-content: center;

  .box {
    width: 60%;
    height: 90%;
    background: #8FBC8F;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo {
      width: 25%;
      height: 90%;
      background: #61bc84;
      border-radius: 15px;
      margin-right: 5%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      >img {
        width: 40%;
      }

      >ul {
        margin-top: 1vh;

        >li {
          font-size: 14px;
          font-weight: bold;
          letter-spacing: 2px;


          >div {
            padding: .25vh .125vw .25vh .125vw;
            background: #e0e0e048;
            border-radius: 5px;
          }
        }
      }
    }

    .open {
      width: 65%;
      height: 90%;
      background: #61bc84;
      border-radius: 15px;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      >ul {
        width: 100%;
        height: 90%;
        display: flex;
        justify-content: left;
        align-items: center;
        position: relative;

        >li {
          height: 100%;
          background: #8fbc8f9c;
          border-radius: 15px;
          margin: 0 1vw 0 1vw;

          >div {
            margin: 0 5vw 1vh 1vw;
            transform: translateY(1vw);
            max-height: 100%;
            height: 50%;
            position: relative;

            >p {
              font-weight: bold;
              font-size: 18px;
              margin-bottom: 15px;
            }

            >ul {

              >li {
                margin-bottom: 10px;
                cursor: pointer;

                >a {
                  display: flex;
                  align-items: center;
                  transition: .7s;
                  color: #1d1e22;

                  .arrow {
                    opacity: 0;
                    transition: .7s;
                  }
                }

                &:hover {
                  >a {
                    color: #2E8B57;
                  }

                  .arrow {
                    color: #2E8B57;
                    opacity: 1;
                    transform: translateX(.5vw);
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  >p {
    position: absolute;
    bottom: 0;
    opacity: .3;
  }
}